
.SwatchEditor {
  border: 1px solid #bbb;
  border-radius: 3px;
  background: #fff;
  padding: 8px;
}

.swatch-title {
  border: none;
  line-height: 1;
  font-size: 13px;
  font-weight: 600;
  display: block;
  width: 100%;
  padding: 4px 0px;
}

.style-list {
  margin-top: 6px;
}

.styles-subtitle {
  font-size: 12px;
  color: #888;
  border-bottom: 1px dotted #cfcfcf;
  display: block;
  padding-bottom: 3px;
}

.add-style {
  display: block;
  width: 50%;
  padding: 4px 0px;
  border-radius: 3px;
  margin: 8px auto 0;
  color: #4da3ff;
  border-color: #4da3ff;
}

.add-style:hover, .add-style:focus {
  color: #007bff;
  border-color: #007bff;
}
