
.NavTab {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.tab-body {
  padding: 40px;
  flex: 1;
}

.tab-header {
  position: relative;
  height: 58px;
  padding-top: 14px;
  padding-left: 30px;
  border-bottom: 1px solid #cdcdcd;
  background: #fff;
}

.tab-header > .Save {
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
}

.tab-header-item {
  background: #f0f0f0;
  height: 44px;
  line-height: 58px;
  font-size: 14px;
  margin: 0px 10px;
  padding: 0px 12px;
  border-radius: 3px 3px 0px 0px;
  border: 1px solid #cdcdcd;
  cursor: pointer;
  position: relative;
}

.tab-header-sub-title {
  border-radius: 3px 3px 0px 0px;
  position: absolute;
  top: 0px;
  font-size: 10px;
  left: 0px;
  right: 0px;
  text-align: left;
  border-bottom: 1px solid #ddd;
  height: 14px;
  line-height: 14px;
  padding-left: 6px;
}

.Build.tab-header-sub-title {
  background-color: #ffd6a5;
  color: #a87219;
}

.Design.tab-header-sub-title {
  background-color: #e5d8ff;
  color: #5555aa;
}

.tab-header-item.active, .tab-header-item:hover {
  color: #007bff;
}

.tab-header-list {
  display: flex;
}

.setup {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
}

.build {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
}

.master {
  display: flex;
  height: 100%;
}

.element-cta {
  width: 100%;
  margin-bottom: 30px;
  font-size: 14px;
  font-style: italic;
  color: #6f6f6f;
}

.element-cta-button {
  position: relative;
  background: none;
  border: none;
  font-size: 14px;
  color: #007bff;
  margin-left: 5px;
}

.element-cta-button.open .template-options {
  display: block;
}

.template-options {
  display: none;
  position: absolute;
  top: calc(100% + 6px);
  background: #fff;
  text-align: left;
  border-radius: 3px;
  padding: 6px 0px;
  box-shadow: 0px 1px 3px -1px #aaa;
}

.template-options li {
  color: #333;
  padding: 4px 15px;
}

.template-options li:hover {
  background-color: #007bff;
  color: #fff;
}
