
.Setup .load {
  padding: 6px 12px;
  background: #007bff;
  color: #fff;
  font-size: 12px;
  border: none;
  border-radius: 3px;
  position: relative;
  margin-bottom: 20px;
}

.Setup .load.disabled {
  background-color: #888;
  opacity: 0.5;
  pointer-events: none;
}

.Setup .load i {
  margin-left: 6px;
}

.Setup .load.open .load-options {
  display: block;
}

.Setup .load-options {
  display: none;
  position: absolute;
  top: calc(100% + 6px);
  border-radius: 4px;
  padding: 6px 0px;
  background-color: #fff;
  box-shadow: 0px 1px 3px -1px #aaa;
}

.Setup .load-option {
  padding: 3px 8px;
  color: #333;
  white-space: nowrap;
  text-align: left;
}

.Setup .load-option:hover {
  background-color: #007bff;
  color: #fff;
}

.name-label {
  display: block;
}

.name-input {
  padding: 6px;
  font-size: 12px;
  line-height: 1;
}
